a/data';\n  import mdl from 'material-design-lite/material.js';\n  import geo from 'gps-util';\n  import auth from '../auth';\n\n  export default {\n    name: 'MapView',\n    data () {\n      return {\n        Map : Object.create(null),\n        Markers: {\n          type: 'FeatureCollection',\n          features: []\n        },\n        Source: {\n          setData () {}\n        },\n        geoHash: ''\n      };\n    },\n    watch: {\n      'Markers.features' () {\n        this.Source.setData(this.Markers);\n      }\n    },\n    route: { },\n    created () {},\n    ready () {\n      // MDL\n      this.$nextTick(function(){\n        componentHandler.upgradeAllRegistered();\n      })\n\n      // Venue Source\n      auth.findAllVenues(this).then((res) => {\n        res.forEach((re) => {\n          this.Markers.features.push({\n            'type': 'Feature',\n            'properties': {\n              'description': '<div class=\"demo-card-wide mdl-card mdl-shadow--2dp\"><div class=\"mdl-card__title\"><h2 class=\"mdl-card__title-text\">'+ re.venueName +'</h2></div><div class=\"mdl-card__supporting-text\">'+ re.other +'</div><div class=\"mdl-card__actions mdl-card--border\"><a href=\"#!/venue/' + re.geoHash + '\" class=\"mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect\"></a></div><div class=\"mdl-card__menu\"><button class=\"mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect\"><i class=\"material-icons\">share</i></button></div></div>',\n              'marker-symbol': 'star'\n            },\n            'geometry': {\n              'type': 'Point',\n              'coordinates': [re.coordinate.lng, re.coordinate.lat]\n            }\n          });\n        });\n      });\n\n      // Map Source\n      let source;\n      this.Map = new DataAPI.MapBox.Map({\n        container: 'map',\n        style: 'mapbox://styles/ccharlieli/cio8phh7x001sadnmu988hd91',\n        center: [121.4691603379, 31.2223104171],\n        zoom: 13\n      });\n\n      this.Map.on('load', () => {\n        this.Source = new DataAPI.MapBox.GeoJSONSource({\n          data: this.Markers,\n          cluster: true,\n          clusterMaxZoom: 14, // Max zoom to cluster points on\n          clusterRadius: 50 // Radius of each cluster when clustering points (defaults to 50)\n        });\n        this.Map.addSource('markers', this.Source);\n        this.Map.addLayer({\n          'id': 'markers',\n          'type': 'symbol',\n          'source': 'markers',\n          'layout': {\n            'icon-image': '{marker-symbol}-15',\n            'icon-allow-overlap': true\n          }\n        });\n\n        let layers = [\n          [150, '#f28cb1'],\n          [20, '#f1f075'],\n          [0, '#6ab344']\n        ];\n        layers.forEach((layer, i) => {\n          this.Map.addLayer({\n            \"id\": \"cluster-\" + i,\n            \"type\": \"circle\",\n            \"source\": \"markers\",\n            \"paint\": {\n              \"circle-color\": layer[1],\n              \"circle-radius\": 18\n            },\n            \"filter\": i == 0 ?\n              [\">=\", \"point_count\", layer[0]] :\n              [\"all\",\n                [\">=\", \"point_count\", layer[0]],\n                [\"<\", \"point_count\", layers[i - 1][0]]]\n          });\n        });\n        // Add a layer for the clusters' count labels\n        this.Map.addLayer({\n          \"id\": \"cluster-count\",\n          \"type\": \"symbol\",\n          \"source\": \"markers\",\n          \"layout\": {\n            \"text-field\": \"{point_count}\",\n            \"text-font\": [\n              \"DIN Offc Pro Medium\",\n              \"Arial Unicode MS Bold\"\n            ],\n            \"text-size\": 12\n          }\n        });\n      });\n\n      let clickFlag = false;\n      this.Map.on('click', (e) => {\n        if (clickFlag){\n          this.Markers.features.pop();\n        }\n        this.geoHash = geo.geohashEncode(e.lngLat.lat, e.lngLat.lng, 7);\n        this.Markers.features.push({\n          'type': 'Feature',\n          'properties': {\n            'description': '<div class=\"demo-card-wide mdl-card mdl-shadow--2dp\"><div class=\"mdl-card__title\"><h2 class=\"mdl-card__title-text\"></h2></div><div class=\"mdl-card__supporting-text\">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris sagittis pellentesque lacus eleifend lacinia...</div><div class=\"mdl-card__actions mdl-card--border\"><a href=\"#!/venue/' + this.geoHash + '\" class=\"mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect\"></a></div><div class=\"mdl-card__menu\"><button class=\"mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect\"><i class=\"material-icons\">share</i></button></div></div>',\n            'marker-symbol': 'star'\n          },\n          'geometry': {\n            'type': 'Point',\n            'coordinates': [e.lngLat.lng, e.lngLat.lat]\n          }\n        });\n        clickFlag = true;\n      });\n\n      // Pop up\n      var popup = new DataAPI.MapBox.Popup({\n        closeButton: false,\n        closeOnClick: false\n      });\n      this.Map.on('mousemove', (e) => {\n        var features = this.Map.queryRenderedFeatures(e.point, { layers: ['markers'] });\n        this.Map.getCanvas().style.cursor = (features.length) ? 'pointer' : '';\n        if (!features.length) {\n          popup.remove();\n          return;\n        }\n        var feature = features[0];\n        popup.setLngLat(feature.geometry.coordinates)\n          .setHTML(feature.properties.description)\n          .addTo(this.Map);\n      });\n    },\n    destroyed () {},\n    methods: {\n      onDetail () {\n        window.console.log(123);\n      }\n    },\n    filters: {}\n  }\n</script>\n\n<style type='text/css'>\n  #map { position:fixed; top:0px; bottom:0px; width:100%;  }\n\n  #menu {\n      background: #fff;\n      position: absolute;\n      z-index: 10;\n      top: 80px;\n      right: 10px;\n      border-radius: 3px;\n      width: 120px;